* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Source Sans Pro", sans-serif;
    font-size: x-large;
  }
  .user-name {
    font-size: x-large;
  }
  h1 {
    color: rgb(15, 15, 15);
    text-align: center;
  }
  html {
    font-size: 62.5%;
  }
  .btn-info {
    font-size: 4rem;
    margin-bottom: 20px;
  }
  .main-div {
    display: flex;
    justify-content: center;
  }
  
  .avatars-container {
    display: flex;
    margin: 1rem;
    flex-direction: row;
    justify-content: flex-end;
  }
  
  .child-div {
    text-align: center;
    margin-top: 50px;
  }
  
  .child-div figure img {
    width: 10rem;
    height: 8rem;
  }
  
  .child-div figure figcaption {
    color: #fff;
    font-size: 2.2rem;
    padding-top: 2rem;
    text-transform: capitalize;
  }
  
  input {
    min-width: 30rem;
    height: 3.4rem;
    padding: 2rem 1.2rem;
    font-size: 3rem;
    line-height: 1.42857143;
    margin-top: 2rem;
    color: rgb(58, 57, 57);
    background-color: #fff;
    background-image: none;
    border: 0.1rem solid #ccc;
    border-radius: 0.4rem;
    -webkit-box-shadow: inset 0 0.1rem 0.1rem rgb(0 0 0 / 8%);
    box-shadow: inset 0 0.1rem 0.1rem rgb(0 0 0 / 8%);
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }
  
  input,
  input:focus,
  input:active,
  input:active:focus {
    border: none;
    padding: 2.3rem 1.5rem;
    outline: none;
  }
  input {
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
  }
  input:focus {
    -webkit-box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
  }
  
  .delete-btn {
    width: 2rem;
    color: #666;
    display: flex;
    justify-content: space-around;
    left: 500px;
  }
  
  .fa {
    margin-left: -2.5rem;
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    background-color: #fff;
    background-image: none;
    color: #666;
    -webkit-transition: color 0.15s linear;
    transition: color 0.15s linear;
    font-size: 3rem;
  }
  .fa:hover {
    color: rgb(47, 214, 122);
  }
  .fa-plus {
    font-size: large;
  }
  .fa-plus:before {
    content: "\f067";
    font-size: 1.5rem;
  }
  .item-div {
    display: inline-block;
  }
  .showItems {
    text-indent: -2em;
  
    margin-top: 3rem;
    text-align: center;
  }
  .my-button {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 100px;
    outline: none;
    background: #868384;
    color: white;
    cursor: pointer;
    left: 200px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  }
  .my-button:hover {
    background: #07fd97;
  }
  .eachItem:hover {
    background: rgb(249, 249, 252);
    color: rgb(85, 41, 220);
  }
  
  .showItems .eachItem h3 {
    padding-left: 1rem;
    font-size: 1.6rem;
    color: #fff;
  }
  
  .fa-trash-alt,
  .fa-edit {
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    color: rgb(117, 117, 117);
    -webkit-transition: color 0.15s linear;
    transition: color 0.15s linear;
    font-size: 1rem;
  }
  
  .addItems {
    display: flex;
    flex-direction: row;
  }
  
  .addItems .fa-edit {
    color: rgb(47, 214, 122);
  }
  
  .fa-edit {
    margin-left: -1.5rem;
  }
  
  .eachItem:hover .fa-trash-alt {
    color: rgb(214, 47, 61);
  }
  
  .eachItem:hover .fa-edit {
    color: rgb(100, 214, 47);
  }
  
  .eachItem:hover h3 {
    color: rgb(85, 41, 220);
  }
  
  .btn {
    letter-spacing: 0.1em;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 45px;
    max-width: 160px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    width: 100%;
  }
  .btn:hover {
    text-decoration: none;
  }
  .add-btn {
    border: aquamarine;
    border-radius: 20px;
    background: none;
    position: absolute;
    left: 600px;
    border: none;
  }
  .remove-all-btn {
    --uismLinkDisplay: var(--smLinkDisplay, inline-flex);
    display: var(--uismLinkDisplay);
    color: #000;
    outline: solid 2px #000;
    position: relative;
    transition-duration: 0.4s;
    overflow: hidden;
  }
  
  .remove-all-btn::before,
  .remove-all-btn span {
    margin: 0 auto;
    transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    transition-duration: 0.4s;
  }
  
  .remove-all-btn:hover {
    background-color: rgb(85, 41, 220);
  }
  
  .remove-all-btn:hover span {
    -webkit-transform: translateY(-400%) scale(-0.1, 20);
    transform: translateY(-400%) scale(-0.1, 20);
  }
  
  .remove-all-btn::before {
    content: attr(data-sm-link-text);
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    -webkit-transform: translateY(500%) scale(-0.1, 20);
    transform: translateY(500%) scale(-0.1, 20);
  }
  
  .remove-all-btn:hover::before {
    letter-spacing: 0.05em;
    -webkit-transform: translateY(0) scale(1, 1);
    transform: translateY(0) scale(1, 1);
  }
  