@import url('https://fonts.googleapis.com/css2?family=Abel&family=Megrim&family=Playfair+Display:wght@400;500;700&family=Poiret+One&display=swap');
.react-calendar { 
  width: 400px;
  height: 100%;
  background-color: white;
  color: white;
  border-radius: 8px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  line-height: 0.8em;
  padding-right: 18px;
  font-family: 'Playfair Display', serif;
  margin-top: 50px;
  padding-bottom: 10px;
}

.react-calendar__navigation button {
  color: rgb(75, 75, 75);
  min-width: 44px;
  background:none;
  font-size: 20px;
  margin-top: 8px;
  text-shadow: 1px 2px 3px #969696;
}

.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  display: flex;
  height: 50px;
margin-bottom: 10px;
}
.react-calendar__navigation button {
  min-width: 34px;
  font-size: 30px;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #e6e6e6;
}
.react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
}
.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
}
.react-calendar__month-view__days__day{
    text-shadow: 1px 2px 3px #969696;
}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
  margin: 1px;
  font-size: 10px;
  font-style: normal;
  color: #d8d8d8;
  text-shadow: 1px 1px 1px #acb58c;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  font-weight: bold;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
}
.react-calendar__month-view__days__day--weekend {
  font-family: 'Playfair Display', serif;
}
.react-calendar__month-view__days__day--neighboringMonth {
  padding: 10px;
  font-family: 'Playfair Display', serif;
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.react-calendar__tile {
  max-width: 100%;
  text-align: center;
  padding: 0.70em 0.15em 0.6em 0.2em ;
  background: none;
}

.react-calendar__navigation__label__labelText{
    font-size: 22px;
    font-weight: bold;
    color: rgb(75, 75, 75);
}
.react-calendar__tile:disabled {
  background-color: #f0f0f0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #e6e6e6;
  border-radius: 100px;
}
.react-calendar__tile--now {
  background-color: #f3f3f3;
  color: black;
  border-radius: 100px;
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  background: #ffffa9;
  border-radius: 100px;
}

.react-calendar__tile--hasActive {
  background: #acb58c;

}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #acb58c;
}
.react-calendar__tile--active {
  background: #acb58c;
  color: white;
  border-radius: 100px;
  
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #acb58c;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}


